.portfolio {
  position: relative;
  padding-bottom: 135px;
  overflow: hidden;
  color: $black;

  .animate-circle {
    left: -355px;
    bottom: 72px;
    height: 607px;
    @include breakpoint(max-width 1400px) {
      display: none;
    }
    p {
      position: absolute;
      right: -188px;
      top: 50%;
      display: flex;
      align-items: center;
      max-width: 340px;
      margin: 0;
      transform: translateY(-50%);
      font-family: $GilroyMedium;
      font-size: 16px;
      line-height: 18px;
      z-index: 2;
      @include breakpoint(max-width 1750px) {
        right: -100px;
      }
      @include breakpoint(max-width 1700px) {
        right: -50px;
      }
      @include breakpoint(max-width 1600px) {
        right: 3px;
      }
      @include breakpoint(max-width 1450px) {
        right: 125px;
      }
      @include breakpoint(max-width 1400px) {
        display: none;
      }
      a {
        display: flex;
        justify-content: center;
        align-items: center;
        min-width: 80px;
        height: 80px;
        margin-right: 15px;
        border-radius: 50%;
        background-color: #3A6FFE;
        box-shadow: -1px -2px 0 0 rgba(#5a99d4, .5);
        animation: pulse 1.5s infinite;
        
        img {
          vertical-align: middle;
          border-radius: 50%;
        }
      }

      span {
        display: inline-block;
        width: 180px;
        @include breakpoint(max-width 1600px) {
          display: none;
        }
      }
    }
  }

  .wrap {
    margin-left: 7%;

    @include breakpoint(max-width 768px) {
      margin: 0 auto;
    }
  }
  .paragraph {
    top: 62px;
    left: -46px;
    letter-spacing: 0.5px;
    &:before {
      top: 0;
    }
  }
  .tittle {
    p {
      width: 475px;
    }
  }

  &__gallery {
    margin-top: 30px;
    @include breakpoint(max-width 992px) {
      min-width: 100%;
    }

  }
}

.portfolio__gallery {
      display: -ms-grid;
      -ms-grid-row-gap: 8px;
      -ms-grid-columns: 20.4vw 13.8vw 9vw 4.8vw 9vw 13.8vw;
      -ms-grid-rows: 13.8vw 13.8vw;

      .portfolio-link {
        position: relative;
        -ms-grid-row: 1;
        transition: opacity 0.2s;

        &:hover {
          opacity: 0.8;
          transition: opacity 0.2s;
        }

        &:first-child:hover {
          opacity: 1;
        }
      }

      .portfolio-soc {
        -ms-grid-column: 2;
        -ms-grid-row: 1;
      }

      .portfolio-angel {
        -ms-grid-column: 4;
        -ms-grid-row: 1;
      }

      .portfolio-scrubb {
        -ms-grid-column: 6;
        -ms-grid-row: 1;
      }

      .portfolio-loft {
        -ms-grid-column: 2;
        -ms-grid-row: 2;
      }

      .portfolio-chair {
        -ms-grid-column: 3;
        -ms-grid-row: 2;
      }

      .portfolio__brief {
        display: flex;
        justify-content: center;
        align-items: center;
        -ms-grid-column: 5;
        -ms-grid-row: 2;
        background-color: #ff4834;
        transition: opacity 0.2s;
        &:hover {
          opacity: 0.8;
          transition: opacity 0.2s;
        }
        h2 {
          margin-right: auto;
          margin-left: 15%;
        }

        a {
          font-size: 60px;
          font-family: 'BebasNeueBold', sans-serif;
          color: white;
          text-transform: uppercase;
        }
      }
    }

@supports (display: grid) {
  .portfolio__gallery {
      display: -ms-grid;
      display: grid;
      grid-gap: 8px 10px;
      grid-template-columns: 20.4vw 13.8vw 9vw 4.8vw 9vw 13.8vw;
      grid-template-rows: 13.8vw 13.8vw;

      img {
        width: 100%;
        height: 100%;
      }

      .portfolio-link {
        position: relative;
        grid-row: 1 / 3;
      }

      .portfolio-soc {
        grid-column: 2 / 4;
        grid-row: 1;
      }

      .portfolio-angel {
        grid-column: 4 / 6;
        grid-row: 1;
      }

      .portfolio-scrubb {
        grid-column: 6 / 7;
        grid-row: 1;
      }

      .portfolio-loft {
        grid-column: 2 / 3;
        grid-row: 2;
      }

      .portfolio-chair {
        grid-column: 3 / 5;
        grid-row: 2;
      }

      .portfolio__brief {
        grid-column: 5 / 7;
        grid-row: 2;
      }

      @include breakpoint(max-width 1280px) {
        grid-template-columns: 392px 69px 196px 265px;
        grid-template-rows: 265px 265px 265px;

        .portfolio-link {
          grid-row: 1 / 3;
        }

        .portfolio-soc {
          grid-column: 1 / 3;
          grid-row: 3;
        }

        .portfolio-angel {
          grid-column: 2 / 4;
          grid-row: 1;
        }

        .portfolio-scrubb {
          grid-column: 4 / 5;
          grid-row: 1;
        }

        .portfolio-loft {
          grid-column: 2 / 4;
          grid-row: 2;
        }

        .portfolio-chair {
          grid-column: 4 / 5;
          grid-row: 2;
        }

        .portfolio__brief {
          grid-column: 3 / 5;
          grid-row: 3;
        }
      }

      @include breakpoint(max-width 1070px) {
        grid-template-columns: 392px 265px;
        grid-template-rows: 265px 265px 265px;

        .portfolio-link {
          grid-row: 1 / 3;
        }

        .portfolio-soc,
        .portfolio-angel,
        .portfolio-chair {
          display: none;
        }

        .portfolio-scrubb {
          grid-column: 2 / 3;
          grid-row: 1;
        }

        .portfolio-loft {
          grid-column: 2 / 3;
          grid-row: 2;
        }

        .portfolio__brief {
          grid-column: 1 / 3;
          grid-row: 3;
        }
      }

      @include breakpoint(max-width 992px) {
        grid-template-columns: 55.7% 42.9%;
        grid-template-rows: 265px 265px 265px;
      }

      @include breakpoint(max-width 576px) {
        grid-template-columns: 100%;
        grid-template-rows: auto auto 265px;

        .portfolio-link {
          grid-row: 1;
        }

        .portfolio-loft {
          grid-column: 1;
          grid-row: 2;
        }

        .portfolio__brief {
          grid-column: 1;
          grid-row: 3;
        }

        .portfolio-scrubb {
          display: none;
        }
      }
    }
}

